const equipment = {
  gongonier: {
    name: "",
    detail: "20%",
    passive: true
  },
  sword_of_feixiang: {
    name: "",
    detail: "+40%",
    passive: true
  },
  sunshade: {
    name: "",
    detail: "30%200%",
    handle: (Admin, Target) => {
      let Health = Admin.role.state.of(0).get().Health;
      Admin.role.state.of(0).set("Health", Health * 0.7);
      Admin.role.buff.of(0).add("barrier", Math.ceil(Health * 0.3 * 2));
    }
  },
  mirror_of_pear: {
    name: "",
    detail: "",
    passive: true
  },
  mini_bagua_stove: {
    name: "",
    detail: "30",
    handle: (Admin, Target) => {
      Admin.enermy.damage.targetAll().source("equipment").by({
        type: "static",
        value: 30
      });
    }
  },
  louguanjian: {
    name: "",
    detail: "20%",
    aim: true,
    handle: (Admin, Target) => {
      let Health = Admin.enermy.state.of(Target).get().Health;
      Admin.enermy.state.of(Target).set("Health", Health * 0.8);
    }
  },
  hd_camera: {
    name: "",
    detail: "",
    aim: true,
    handle: (Admin, Target) => {
      Admin.enermy.buff.of(Target).add("exposure");
    }
  },
  repentance_rod: {
    name: "",
    detail: "",
    passive: true
  },
  circular_fan: {
    name: "",
    detail: "11",
    passive: true
  },
  devil_tied_scroll: {
    name: "",
    detail: "",
    aim: true,
    handle: (Admin, Target) => {
      Admin.enermy.buff.of(Target).clearAll(true);
    }
  }
}

for (let e in equipment) equipment[e].type = "gold";

export default equipment;