const lilywhite = {
  name: "",
  intro: "",
  detail: "",
  type: "normal",
  scene: ["shrine", "forest"],
  growth: {
    health: 0.8,
    attack: 1.2,
    speed: 1
  },
  action: (Admin, index) => {
    let r = Math.random();
    Admin.role.damage.target(0).from(index).by({
      type: "scale",
      value: 200 - r * 100,
    });
    Admin.enermy.buff.of(index).clearAll();
  }
}

export default lilywhite;