const eternity = {
  name: "",
  intro: "",
  detail: "[]",
  type: "normal",
  scene: ["shrine", "forest"],
  growth: {
    health: 1.2,
    attack: 1,
    speed: 1.3
  },
  action: (Admin, index) => {
    Admin.role.damage.target(0).from(index).by({
      type: "scale",
      value: 100,
      penetrate: true
    });
    Admin.role.buff.of(0).add("trance", 2);
  }
}

export default eternity;