const mokou = {
  name: "",
  intro: "",
  detail: "",
  type: "boss",
  growth: {
    health: 4,
    attack: 2,
    speed: 1.5
  },
  action: function (Admin, index) {
    let state = Admin.enermy.state.of(index).get();
    let hurt = state.health - state.Health;
    Admin.role.damage.target(0).by({
      type: "static",
      value: hurt / 2
    });
    Admin.enermy.heal.target(index).by({
      type: "static",
      value: hurt / 2
    });
  }
}

export default mokou;