const chen = {
  name: "",
  intro: "",
  detail: "",
  type: "elite",
  scene: ["shrine", "town"],
  growth: {
    health: 2,
    attack: 2,
    speed: 1.7
  },
  action: (Admin, index) => {
    Admin.role.damage.target(0).from(index).by({
      type: "scale",
      value: 40,
      amount: 4,
      penetrate: true
    });
    Admin.role.buff.of(0).add("bleed", 1);
  },
  onload: (Admin, index) => {
    if (Admin.role.state.of(0).get().speed < Admin.enermy.state.of(index).get().speed)
      Admin.role.damage.target(0).from(index).by({
        type: "scale",
        value: 80,
        penetrate: true
      });
  }
}

export default chen;