const star = {
  name: "",
  intro: "",
  detail: "50%",
  type: "normal",
  scene: ["forest", "town"],
  growth: {
    health: 0.8,
    attack: 1,
    speed: 1
  },
  action: (Admin, index) => {
    Admin.role.damage.target(0).from(index).by({
      type: "scale",
      value: 100,
    });
    Admin.role.buff.of(0).add("weak", 2*Admin.role.event.round.count);
    Admin.enermy.buff.addAll("strength", 2);
    Admin.enermy.buff.of(index).set("cautious", 5);
  }
}

export default star;